@use 'mixins' as mixins;

:host {
  .game-item-btn-wrapper {
    width: 100%;
    height: 100%;
    position: relative;
    user-select: none;

    .surface {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      z-index: 2;
    }

    .blocked-item {
      filter: grayscale(80%);
    }

    .priced-item {
      // Todo add smth special for priced btn;
    }

    .game-item-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      border: 1px solid var(--mat-sys-primary-container);
      background: radial-gradient(
          circle at 50%, var(--mat-sys-primary), var(--mat-sys-background) 70%
      );

      .game-item {
        position: relative;
        @include mixins.vm-media(horizontal,
            (width, (calc(100% - 38px))),
            (height, (calc(100% - 38px))),
        );

        &:before {
          content: '';
          position: absolute;
          left: -13px;
          right: -13px;
          top: -13px;
          bottom: -13px;
          border-radius: 50%;
          background: var(--mat-sys-surface-container);
        }

        .available-icon {
          position: absolute;
          width: 100%;
          height: 100%;
        }

        .small-icon {
          width: 85% !important;
          height: 85% !important;
          left: 50% !important;
          top: -10% !important;
          translate: -50% !important;
        }

        .large-icon {
          width: 120% !important;
          height: 120% !important;
          left: 50% !important;
          top: -20% !important;
          translate: -50% !important;
        }
      }
    }

    .tag {
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      bottom: 0;
      left: 0;
      overflow: hidden;
      text-align: center;
      border-radius: 100px;
      border: 1px solid var(--mat-sys-primary-container);
      z-index: 1;
      pointer-events: none;
      font-family: "Roboto Light", sans-serif;
      background: linear-gradient(
          to top, var(--mat-sys-primary) -100%, var(--mat-sys-background) 10%, var(--mat-sys-primary) 300%
      );
      color: var(--mat-sys-on-primary-container);
      @include mixins.vm-media(
          horizontal,
            (height, (25px, 30px)),
            (font-size, (14px, 16px)),
            (padding, (3px 7px 3px 10px, 4px 8px 4px 12px, 5px 10px 5px 15px))
      );
      &:has(.name), &:has(.blocked) {
        @include mixins.vm-media(
            horizontal,
              (padding, (3px 7px 3px 7px, 4px 8px 4px 8px, 5px 10px 5px 10px))
        );
      }
    }

    &:hover:not(:has(.blocked-item)) .tag, &.focused .tag {
      background: linear-gradient(
          to top, var(--mat-sys-primary) -100%, var(--mat-sys-background) 10%, var(--mat-sys-primary) 200%
      ) !important;
    }

    &:hover:not(:has(.blocked-item)) .game-item-btn, &.focused .game-item-btn {
      background: radial-gradient(
          circle at 50%, var(--mat-sys-primary), var(--mat-sys-background) 80%
      ) !important;
    }

    .blocked-icon {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      color: var(--mat-sys-on-primary-container);
      @include mixins.vm-media(horizontal,
          (width, (20px, 25px)),
          (height, (20px, 25px)),
          (font-size, (20px, 25px))
      );
    }

    .name {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      margin: auto;
      width: 100%;
    }
  }
}
